/* ----------------------------------------------------------------------------------
 * Main title (.title-card.section):
 * The top section.
 */
.title-card {
  background-color: #b61521;
  color: #fcf4d6;
  text-shadow: 0 0 5px #b61521;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.4);
  border-bottom: solid 2px #212121; }
  .title-card .bg {
    overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%; }
  @media (max-width: 480px) {
    .title-card {
      background-image: url(../images/bg-title-m.jpg);
      background-size: 100% auto;
      background-position: center top;
      background-repeat: no-repeat; }
      .title-card .bg {
        display: none; } }
  .title-card .content {
    position: relative;
    z-index: 5;
    padding-top: 40px;
    padding-bottom: 40px; }
    @media (max-width: 480px) {
      .title-card .content {
        padding-bottom: 20px; } }

/* ----------------------------------------------------------------------------------
 * Title logo
 * The logo image thing.
 */
.title-card .logo {
  width: 290px; }
@media (min-width: 480px) {
  .title-card .logo {
    width: 300px; } }
@media (min-width: 768px) {
  .title-card .logo {
    width: 400px; } }
.title-card .logo {
  height: 99.6875px; }
@media (min-width: 480px) {
  .title-card .logo {
    height: 103.125px; } }
@media (min-width: 768px) {
  .title-card .logo {
    height: 137.5px; } }
.title-card .logo .styled, .title-card .logo .normal {
  display: none; }
@media (max-width: 480px) {
  .title-card .logo {
    margin: 50px auto 80px auto; }
    .title-card .logo .styled {
      display: block; } }
@media (min-width: 480px) {
  .title-card .logo {
    margin-left: auto;
    margin-bottom: 300px; }
    .title-card .logo .normal {
      display: block; } }
@media (min-width: 768px) {
  .title-card .logo {
    margin-bottom: 400px; } }
.title-card .logo img {
  width: 100%; }

/* ----------------------------------------------------------------------------------
 * Share links:
 * Facebook and twitter buttons.
 */
.share-links > * {
  display: inline-block; }
.share-links {
  text-shadow: none; }
@media (min-width: 480px) {
  .share-links {
    background: white;
    padding: 10px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    height: 40px;
    max-width: 280px; } }
@media (min-width: 480px) {
  .share-links {
    position: absolute;
    top: 40px;
    left: 40px; } }
@media (max-width: 480px) {
  .share-links {
    margin: 20px auto;
    text-align: center; } }

/* ----------------------------------------------------------------------------------
 * Details:
 * "Conference on creativity" etc
 */
.title-card .details {
  clear: both; }
  .title-card .details:after {
    content: "";
    display: table;
    clear: both; }
  .title-card .details {
    margin-top: 40px; }

.title-card .detail {
  border-top: solid 2px #ff6555;
  margin-right: 20px;
  padding-top: 10px;
  line-height: 1.4;
  padding-bottom: 20px; }
@media (min-width: 480px) {
  .title-card .detail {
    padding-bottom: 40px; } }
.title-card .detail em, .title-card .detail strong {
  display: block; }
.title-card .detail strong, .title-card .detail h2 {
  font-family: oswald, sans-serif;
  letter-spacing: 1px;
  font-weight: 300;
  text-transform: uppercase;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: block; }
  .title-card .detail strong, .title-card .detail h2 {
    font-size: 1.2em; }
  @media (min-width: 480px) {
    .title-card .detail strong, .title-card .detail h2 {
      font-size: 1.3em; } }
  @media (min-width: 768px) {
    .title-card .detail strong, .title-card .detail h2 {
      font-size: 1.6em; } }
.title-card .detail em {
  font-family: museo-slab, sans-serif;
  font-weight: 800;
  letter-spacing: -1px;
  text-transform: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #a6d01d;
  font-size: 1.1em; }
  @media (max-width: 480px) {
    .title-card .detail em {
      color: #ff6555; } }

/* ----------------------------------------------------------------------------------
 * Arrangements
 */
.title-card .detail.date, .title-card .detail.venue {
  float: left; }
  .title-card .detail.date, .title-card .detail.venue {
    float: left;
    display: block;
    margin-right: 3.57866%;
    width: 48.21067%; }
    .title-card .detail.date:last-child, .title-card .detail.venue:last-child {
      margin-right: 0; }
  @media (min-width: 480px) {
    .title-card .detail.date, .title-card .detail.venue {
      float: left;
      display: block;
      margin-right: 2.35765%;
      width: 31.76157%; }
      .title-card .detail.date:last-child, .title-card .detail.venue:last-child {
        margin-right: 0; } }
  @media (min-width: 768px) {
    .title-card .detail.date, .title-card .detail.venue {
      float: left;
      display: block;
      margin-right: 2.35765%;
      width: 23.23176%; }
      .title-card .detail.date:last-child, .title-card .detail.venue:last-child {
        margin-right: 0; } }
.title-card .detail.date {
  clear: both; }
.title-card .detail.summary {
  margin-bottom: 10px; }
@media (min-width: 480px) {
  .title-card .detail.summary {
    margin-bottom: 40px; } }
@media (min-width: 768px) {
  .title-card .detail.summary {
    margin-bottom: 60px; } }
@media (max-width: 480px) {
  .title-card .detail.summary {
    margin-right: 0; } }
@media (min-width: 480px) {
  .title-card .detail.summary {
    float: left;
    display: block;
    margin-right: 2.35765%;
    width: 65.88078%; }
    .title-card .detail.summary:last-child {
      margin-right: 0; } }
@media (min-width: 768px) {
  .title-card .detail.summary {
    float: left;
    display: block;
    margin-right: 2.35765%;
    width: 48.82117%; }
    .title-card .detail.summary:last-child {
      margin-right: 0; } }

/* ----------------------------------------------------------------------------------
 * Summary
 * "Conference on creativity"
 */
.title-card .detail.summary {
  line-height: 1.3; }
.title-card .detail.summary h2 {
  font-family: museo-slab, sans-serif;
  font-weight: 800;
  letter-spacing: -1px;
  text-transform: none;
