html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button {
  margin: 0;
  padding: 0;
  cursor: pointer;
  border: 0;
  background: transparent; }

@font-face {
  font-family: "FontAwesome";
  src: url("//netdna.bootstrapcdn.com/font-awesome/4.0.1/fonts/fontawesome-webfont.eot?v=4.0.1");
  src: url("//netdna.bootstrapcdn.com/font-awesome/4.0.1/fonts/fontawesome-webfont.eot?#iefix&v=4.0.1") format("embedded-opentype"), url("//netdna.bootstrapcdn.com/font-awesome/4.0.1/fonts/fontawesome-webfont.woff?v=4.0.1") format("woff"), url("//netdna.bootstrapcdn.com/font-awesome/4.0.1/fonts/fontawesome-webfont.ttf?v=4.0.1") format("truetype"), url("//netdna.bootstrapcdn.com/font-awesome/4.0.1/fonts/fontawesome-webfont.svg?v=4.0.1#fontawesomeregular") format("svg");
  font-weight: normal;
  font-style: normal; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

input:focus, select:focus {
  outline: 0; }

html {
  color: #30303a;
  background: white;
  font-size: 14px;
  overflow-y: scroll; }
  @media (max-width: 480px) {
    html {
      font-size: 13px; } }

html, body {
  height: 100%; }

body {
  line-height: 1.5; }

body, td, input, textarea, select, button {
  font-family: oxygen, sans-serif;
  font-weight: 400; }

a {
  color: #b61521;
  text-decoration: none;
  -webkit-transition: color 40ms linear;
  -moz-transition: color 40ms linear;
  transition: color 40ms linear; }

.fillsize {
  position: relative;
  overflow: hidden; }

.section.tickets, .section.faq, .section.schedule, .section.about, .section.contact {
  border-top: solid 1px rgba(0, 0, 0, 0.15);
  box-shadow: inset 0 4px 5px -4px rgba(0, 0, 0, 0.1);
  background-color: white; }
  .section.tickets, .section.faq, .section.schedule, .section.about, .section.contact {
    padding-top: 20px; }
  @media (min-width: 480px) {
    .section.tickets, .section.faq, .section.schedule, .section.about, .section.contact {
      padding-top: 140px; } }
  .section.tickets, .section.faq, .section.schedule, .section.about, .section.contact {
    padding-bottom: 20px; }
  @media (min-width: 480px) {
    .section.tickets, .section.faq, .section.schedule, .section.about, .section.contact {
      padding-bottom: 140px; } }
.section {
  position: relative;
  z-index: 10; }
.section > .content {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  max-width: 1000px;
  margin: 0 auto;
  position: relative; }
  .section > .content:after {
    content: "";
    display: table;
    clear: both; }
  .section > .content {
    padding-left: 10px;
    padding-right: 10px; }
  @media (min-width: 480px) {
    .section > .content {
      padding-left: 40px;
      padding-right: 40px; } }

/* ----------------------------------------------------------------------------------
 * Temporarily disable other stuff
 */
@media (max-width: 480px) {
  .section.speakers, .section.about, .section.faq, .section.contact {
    display: none !important; } }
