.heading {
  font-size: 2.5em; }
  .heading {
    padding: 20px 0; }
  @media (min-width: 480px) {
    .heading {
      padding: 40px 0; } }
  .heading {
    font-size: 1.8em; }
  @media (min-width: 480px) {
    .heading {
      font-size: 2.2em; } }
  @media (min-width: 768px) {
    .heading {
      font-size: 2.5em; } }
  .heading h2 {
    font-family: oswald, sans-serif;
    letter-spacing: 1px;
    font-weight: 300;
    text-transform: uppercase;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }
    .heading h2:after {
      content: "";
      display: block;
      width: 100%;
      height: 0;
      border-top: solid 1px rgba(0, 0, 0, 0.1); }
      .heading h2:after {
        margin-top: 10px; }
      @media (min-width: 480px) {
        .heading h2:after {
          margin-top: 20px; } }

.faq-list ul:after {
  content: "";
  display: table;
  clear: both; }
.faq-list li {
  float: left;
  display: block;
  margin-right: 2.35765%;
  width: 31.76157%;
  border-top: solid 2px #01bab5;
  padding-top: 20px;
  margin-bottom: 40px; }
  .faq-list li:last-child {
    margin-right: 0; }
.faq-list strong {
  display: block;
  font-family: oswald, sans-serif;
  letter-spacing: 1px;
  font-weight: 300;
  text-transform: uppercase;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.2em;
  color: #01bab5; }
.faq-list p {
  display: none;
  padding-bottom: 20px; }
.faq-list li.active p {
  display: block; }

.section.about, .section.contact {
  background: #212121;
  color: #999999; }
  .section.about h2, .section.contact h2 {
    color: #fcf4d6; }
  .section.about p, .section.contact p {
    width: 66%;
    margin-bottom: 20px;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }
