    background-image: linear-gradient(to bottom, white 0px, rgba(0, 0, 0, 0.3) 1px, rgba(255, 255, 255, 0) 2px, rgba(255, 255, 255, 0));
    background-size: 20px 20px; }
  .speakers.magic .speaker:after {
