/* ----------------------------------------------------------------------------------
 * Background
 */
@media (max-width: 480px) {
  .speakers > .bg {
    display: none; } }
.speakers > .bg .content {
  padding-top: 40px;
  position: relative;
  z-index: 20;
  height: 100%; }
.speakers > .bg {
  background: #212121;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0; }
.speakers > .bg .item {
  position: relative;
  overflow: hidden;
  border-top: solid 2px white; }
.speakers > .bg .items {
  -webkit-transition: 600ms ease;
  -moz-transition: 600ms ease;
  transition: 600ms ease; }
.speakers > .bg .item:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-pointer-events: none;
  -moz-pointer-events: none;
  pointer-events: none;
  z-index: 10;
  background-image: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 50%, rgba(0, 18, 18, 0.7));
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgba(0, 18, 18, 0.7)); }

/* Background fading
 */
.speakers > .bg {
  opacity: 0.01;
  -webkit-transition: opacity 500ms linear;
  -moz-transition: opacity 500ms linear;
  transition: opacity 500ms linear; }
  .section-schedule .speakers > .bg, .section-tickets .speakers > .bg, .section-speakers .speakers > .bg {
    opacity: 1; }

