  -o-transform: translate3d(0, 0%, 0);
  transform: translate3d(0, 0%, 0); }
.section-speaker-1 .speakers .items {
  -webkit-transform: translate3d(0, -16.66667%, 0);
  -moz-transform: translate3d(0, -16.66667%, 0);
  -ms-transform: translate3d(0, -16.66667%, 0);
  -o-transform: translate3d(0, -16.66667%, 0);
  transform: translate3d(0, -16.66667%, 0); }
.section-speaker-2 .speakers .items {
  -webkit-transform: translate3d(0, -33.33333%, 0);
  -moz-transform: translate3d(0, -33.33333%, 0);
  -ms-transform: translate3d(0, -33.33333%, 0);
  -o-transform: translate3d(0, -33.33333%, 0);
  transform: translate3d(0, -33.33333%, 0); }
.section-speaker-3 .speakers .items {
  -webkit-transform: translate3d(0, -50%, 0);
  -moz-transform: translate3d(0, -50%, 0);
  -ms-transform: translate3d(0, -50%, 0);
  -o-transform: translate3d(0, -50%, 0);
  transform: translate3d(0, -50%, 0); }
.section-speaker-4 .speakers .items {
  -webkit-transform: translate3d(0, -66.66667%, 0);
  -moz-transform: translate3d(0, -66.66667%, 0);
  -ms-transform: translate3d(0, -66.66667%, 0);
  -o-transform: translate3d(0, -66.66667%, 0);
  transform: translate3d(0, -66.66667%, 0); }
.section-speaker-5 .speakers .items {
  -webkit-transform: translate3d(0, -83.33333%, 0);
  -moz-transform: translate3d(0, -83.33333%, 0);
  -ms-transform: translate3d(0, -83.33333%, 0);
  -o-transform: translate3d(0, -83.33333%, 0);
  transform: translate3d(0, -83.33333%, 0); }
.section-top .speakers .items {
  -webkit-transform: translate3d(0, 0, 0);
